@import url(https://fonts.googleapis.com/css?family=Montserrat|Raleway&display=swap);
* {
  color: white !important;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Raleway', 'Montserrat', 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* background-color: #fff; */
  background-image: url(/static/media/bg.33496010.jpg);

  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;

  background-attachment: fixed;
}

.root {
  height: 100vh;
}

.ui.menu.footer.text.bottom.attached {
  top: 100vh !important;
  bottom: 0 !important;
  position: -webkit-sticky;
  position: sticky;
  justify-content: center;
  text-align: center;
  border-bottom: none;
  border-left: none;
  border-right: none;
}

.ui {
  font-family: "Raleway", "Montserrat" !important;
  font-size: 1.5rem;
  /* letter-spacing: 2px; */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.ui.secondary.pointing.menu {
  border-bottom: none;
  font-size: large;
}

.ui.secondary.pointing.menu .item {
  letter-spacing: 2px;
  text-transform: uppercase;
}

.ui.secondary.pointing.menu .active.item {
  border-color: white;
}

.ui.secondary.pointing.menu .active.item:hover {
  border-color: white;
}

.ui.secondary.pointing.menu .disabled {
  color: #ddd !important;
}

.ui.secondary.pointing.menu .disabled.item:hover {
  color: white !important;
}

.ui .card {
  background-color: transparent;
}

.ui .divider {
  border-bottom: 1px solid rgba(255, 255, 255, 0.4) !important;
  padding: 3px !important;
  border-top: transparent !important;
}

.ui .label {
  background: rgba(0, 0, 0, 0.3);
}

.ui .label .white {
  color: white !important;
  opacity: 1;
}
.App {
  text-align: center;
  margin: 25px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
